import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
        <section id="contact">
  <div className="row section-intro">
    <div className="col-twelve">
      <h5>Contact</h5>
  
      <p className="lead">Want to know more about me or my projects? My email is always open.</p>
    </div> 
  </div> {/* /section-intro */}

  <div className="row contact-info">
   
    <div className="col tab-full collapse">
      <div className="icon">
        <i className="icon-mail" />
      </div>
      <h5>Email Me At</h5>
      <p>galdre@cmgaldre.com<br />
        cmgaldre@gmail.com          
      </p>
    </div>
  
  </div> {/* /contact-info */}
</section> {/* /contact */}

      </React.Fragment>
    );
  }
}