import React, { Component } from 'react';
export default class Home extends Component {
  render() {
    let introData = this.props.introData;
    return (
      <React.Fragment>
        <section id="intro">   
          <div className="intro-overlay" /> 
          <div className="intro-content">
            <div className="row">
              <div className="col-twelve">
                <h5></h5>
                <h1>{introData.name}</h1>
                <p className="intro-position">
                  <span>{introData.primary}</span>
                  <span>{introData.secondary}</span> 
                </p>
                <a className="button stroke smoothscroll" href="#about">More About Me</a>
              </div>  
            </div>          
          </div> {/* /intro-content */} 
          <ul className="intro-social">              
            <li><a href={'http://www.twitter.com/'+ introData.twitter} target="_blank"><i className="fa fa-twitter" /></a></li>
            <li><a href={'http://www.instagram.com/'+ introData.instagram} target="_blank"><i className="fa fa-instagram" /></a></li>
              <li><a href={'http://www.github.com/'+ introData.twitter} target="_blank"><i className="fa fa-github" /></a></li>
          </ul> {/* /intro-social */}       
       </section>
    </React.Fragment>
    );
  }
}