import React, { Component } from 'react';
export default class About extends Component {
  render() {
    let aboutData = this.props.aboutData;
    return (
      <React.Fragment>
      <section id="about">  
  <div className="row section-intro">
    <div className="col-twelve">
      <h5>About</h5>
      <h1>A bolt of lightning splits the air</h1>
      <div className="intro-info">
        <img src="https://cmgaldre.github.io/aboutme/Kards/images/profile-pic.JPG" alt="Profile Picture" />
        <p className="lead">The lights flicker and fail. In the darkness a mote of light dances down from the stormy sky. 
        It falls silently down the chimney and comes to rest in the ashes of a long dead fire. An explosion shakes the room.
        Dusting soot from a black denim jacket, a figure stands before you. "I heard someones was maybe looking for a web developer?" 
        the figure says as they try to put back a brick knocked loose from the mantle. "My name's Cen" </p>
      </div>        
    </div>      
  </div> 
  <div className="row about-content">
    <div className="col-six tab-full">
      <h3>Profile</h3>
      <p>A web developer from the elder days, Cen's first experience in web development was figuring out how
      to get a Diablo gif on his Geocities page. Something to drive that traffic counter up. </p>
      <ul className="info-list">
        <li>
          <strong>Fullname:</strong>
          <span>{aboutData.name}</span>
        </li>
        <li>
          <strong>Profession:</strong>
          <span>{aboutData.job}</span>
        </li>
        <li>
          <strong>Website:</strong>
          <span>{aboutData.website}</span>
        </li>
        <li>
          <strong>Email:</strong>
          <span>{aboutData.email}</span>
        </li>
      </ul>
      <h3>Bonus Competencies</h3>
      <dl>
      <dt>Environments/Tools</dt>
      <dd><ul className="stats-tabs">
          <li>Sublime</li>
          <li>Postman</li>
          <li>MSSQL Developer Studio</li>
          <li>MS Visual Studio</li>
          <li>MonoDevelop</li>
          <li>NetBeans</li>
          </ul>
        </dd>
      <dt>UI/UX</dt>
      <dd><ul className="stats-tabs">
          <li>Adobe Illustrator</li>
          <li>Adobe Photoshop</li>
          <li>Balsamiq Mockups</li>
          <li>Mural.ly</li>
          <li>Axure</li>
         </ul>
        </dd>
      <dt>Repository/Code Management</dt>
      <dd><ul className="stats-tabs">
          <li>GitHub</li>
          <li>Gitlab</li>
          <li>Cloud Foundry</li>
          <li>Bower</li>
          <li>NPM</li>
          <li>Bluemix</li>
         </ul>
      </dd>
        <dt>Communication</dt>
      <dd><ul className="stats-tabs">
          <li>Slack</li>
          <li>Skype </li>
          <li>Discord</li>
          </ul>
      </dd>
    </dl>      

    </div>
    <div className="col-six tab-full">
      <h3>Skills</h3>
      <p>As a professional and hobbyist Cen has worked in a wide array of languages and frameworks.</p>
      
      <dl>
      <dt>Javascript - 12 Years</dt>
      <dd><ul className="skill-bars">
          <li><div className="progress percent25"><span>2 Years</span></div><strong>React</strong></li>
          <li><div className="progress percent50"><span>5 Years</span></div><strong>Angular</strong></li>
          <li><div className="progress percent50"><span> 5  Years</span></div><strong>Node</strong></li>
          <li><div className="progress percent40"><span> 4 Years</span></div><strong>Backbone</strong></li>
          <li><div className="progress percent40"><span> 4 Years</span></div><strong>Ajax</strong></li>
          </ul></dd>
      <dt>CSS - 10 Years</dt>
      <dd><ul className="skill-bars">
            <li><div className="progress percent40"><span>4 Years</span></div><strong>Less</strong></li>
            <li><div className="progress percent40"><span>4 Years</span></div><strong>Sass</strong></li>
            <li><div className="progress percent50"><span>5 Years</span></div><strong>Media Queries</strong></li>
            </ul>
      </dd>
      <dt>PHP - 7 years</dt>
      <dd><ul className="skill-bars">
      <li><div className="progress percent35"><span>2 Years</span></div><strong>CodeIgniter(MVC)</strong></li>
      </ul></dd>
      <dt>Database 10 years</dt>
      <dd><ul className="skill-bars">
      <li><div className="progress percent100"><span>10 years</span></div><strong>T-SQL/MSSQL</strong></li>
      <li><div className="progress percent60"><span>6 years</span></div><strong>MySQL</strong></li>
      <li><div className="progress percent20"><span>2 years</span></div><strong>MongoDB/NOSQL</strong></li>
      </ul></dd>
      <dt>HTML 14 years</dt>
      <dd><ul className="skill-bars">
      <li><div className="progress percent50"><span>6 years</span></div><strong>Semantic Practices</strong></li>
      <li><div className="progress percent40"><span>5 years</span></div><strong>Responsive Practices</strong></li>
      </ul></dd>
      </dl>
    </div>
  </div>
  <div className="row button-section">
    <div className="col-twelve">
      <a href="#contact" title="Hire Me" className="button stroke smoothscroll">Hire Me</a>
      <a href="/downloads/Cen-Galdre-Web-Developer.pdf" title="Download Resume" className="button button-primary">Download Resume</a>
    </div>      
  </div>
</section>

      </React.Fragment>
    );
  }
}